export function translate(Vue, VueI18n, Vuetify) {
  Vue.use(VueI18n)
  const messages = {
    pt: {
      $vuetify: {
        dataIterator: {
          rowsPerPageText: "Itens por pgina:",
          rowsPerPageAll: "Todos",
          pageText: "{0}-{1} de {2}",
          noResultsText: "Nenhum dado encontrado",
          nextPage: "Prxima pgina",
          prevPage: "Pgina anterior"
        },
        dataTable: {
          rowsPerPageText: "Linhas por pgina:"
        },
        noDataText: "No h dados disponveis"
      }
    }
  }
  const i18n = new VueI18n({
    locale: "pt",
    messages
  })

  Vue.use(Vuetify, {
    lang: {
      t: (key, ...params) => i18n.t(key, params)
    }
  })
}
